import React, { useState, useEffect } from "react";
import { Box, Button } from "@mui/material"

export const AccountSubscription = () => {
    const [isSubscribed, setIsSubscribed] = useState(false);
    const [familyKey, setFamilyKey] = useState(null);

    function sendFamilyKey(familyKey) {
        if(familyKey !== null) {
            fetch(`http://localhost:8081/checkFamilyKey`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    "Authorization": "Bearer " + sessionStorage.getItem("token")
                },
                body: JSON.stringify({ familyKey: Number(familyKey) }),
            })
            .then((response) => response.json())
            .then((data) => {

                if (data.status !== "Ce code existe déjà") {
                    fetch(`http://localhost:8081/updateSubscriptionKey/${sessionStorage.getItem("userId")}`, {
                        method: "PUT",
                        headers: {
                            "Content-Type": "application/json",
                            "Authorization": "Bearer " + sessionStorage.getItem("token")
                        },
                        body: JSON.stringify({
                            "subscriptionKey": 2,
                            "familyKey": familyKey,
                        }),
                    })
                    .then((data) => {
                        sessionStorage.setItem("subscriptionKey", 2);
                        sessionStorage.setItem("familyKey", familyKey);
                        setIsSubscribed(true)
                    })
                    .catch((error) => console.log("error", error));
                }
            })
            .catch((error) => console.log("error", error));
        }
    }
    // nom de chemin de route /updateSubscriptionKey $data['subscriptionKey'] $data['userId']
    function classicSubscription() {
        // fetch(`http://localhost:8081/updateSubscriptionKey/${sessionStorage.getItem("userId")}&subscriptionKey=1`, {
        //     method: "PUT",
        //     headers: {
        //         "Content-Type": "application/json",
        //         "Authorization": "Bearer " + sessionStorage.getItem("token")
        //     }
        // })
        // .then((data) => {
        //     console.log(data);
        //     setIsSubscribed(true)
        // })
        // .catch((error) => console.log("error", error));
    }

    function familySubscription() {
        // générer un nombre qui possède 6 chiffres aléatoire 
        // function setFamilyKey(number) {
        // setFamilyKey(number);
        // }
        const randomNumber = Math.floor(Math.random() * 1000000);

        console.log(randomNumber.toString().padStart(6, '0'));
        // setFamilyKey(randomNumber.toString().padStart(6, '0'));
        sendFamilyKey(randomNumber.toString().padStart(6, '0'));
    }

    function ChangeSuscribeType() {
        return setIsSubscribed(false);
    }
    const styles = {
        BlocsToSubscribe: {
            display: "flex",
            justifyContent: "space-evenly",
            alignItems: "center",
            margin: "2%"
        },
        Blocs: {
            display: "flex",
            flexDirection: "column",
            justifyContent: "center",
            alignItems: "center",
            margin: "2%",
            backgroundColor: "white",
            padding: "5%",
            width: "330px",
        }, 
        Button: {
            backgroundColor: "black",
            color: "wheat",
            fontSize: "unset",
            "&:hover": {
                backgroundColor: "wheat",
                color: "black"
            },
        }
    }
    return (
        <>
            <Box>
                {isSubscribed && (
                    <Button onClick={ChangeSuscribeType}>Would you like change your surscribe ?</Button>
                )}
                {!isSubscribed && (
                    <>
                        <h2>Choose your subscription</h2>
                        <Box sx={styles.BlocsToSubscribe}>
                            <Box sx={styles.Blocs}>
                                <h3>Classic subscription</h3>
                                <p>For 4,99€/month, you can create a classic account</p>
                                <Button sx={styles.Button} onClick={classicSubscription}>Subscribe</Button>
                            </Box>
                            <Box sx={styles.Blocs}>
                                <h3>Family subscription</h3>
                                <p>For 6,99€/month, you can create a family account and add up to 5 members</p>
                                <Button sx={styles.Button} onClick={familySubscription}>Subscribe</Button>
                            </Box>
                        </Box>
                    </>
                )}
            </Box>
        </>
    )
}